body {
  background-color: #eee;
}

#search-clear {
  position: absolute;
  top: 8px;
}

#search-label {
  position: absolute;
  top: 12px;
  left: 12px;
}

.card-image {
  max-height: 120px;
}

.card-image > img {
  -webkit-filter: blur(6px);
  filter: blur(6px);
}

.qr-frame > div > img {
  width: 100% !important;
  height: 100% !important;
  margin: 0 auto;
  display: block;
}

.close-reveal {
  display: inline-block;
  position: absolute;
  top: 4px;
  right: 4px;
}

.card-reveal {
  padding: 0 !important;
}

.qr-frame {
  position: absolute;
  top: 10%;
  bottom: 10%;
  right: 10%;
  left: 10%;
  background-color: #fff;
}

.no-margin {
  margin: 0;
}

.background {
  height: 100%;
}

.button-collapse {
  display: block !important;
}

.disclaimer {
  padding: 32px;
  padding-top: 0;
  padding-bottom: 0;
}

@media screen and (max-width: 600px) {
  .qr-frame {
    top: 0;
  }
}
